import React, { useState } from 'react';
import { FormattedMessage } from 'react-intl';

export const Setup = ({ onGameStart }) => {
  const [config, setConfig] = useState({
    totalPlayers: 8,
    werewolves: 2,
    seers: 1,
    bodyguards: 1,
    gameCode: Math.random().toString(36).substring(2, 8).toUpperCase(),
    gameMasterName: 'Game Master'
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('Setup: Submitting form with config:', config);
    try {
      await onGameStart({
        ...config,
        players: [config.gameMasterName] // Start with the game master as the first player
      });
      console.log('Setup: Game start completed');
    } catch (error) {
      console.error('Setup: Error starting game:', error);
      alert('Failed to start game: ' + error.message);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setConfig(prev => ({
      ...prev,
      [name]: parseInt(value, 10)
    }));
  };

  return (
    <div className="setup-form">
      <h2><FormattedMessage id="game.setup" /></h2>
      <p className="game-code"><FormattedMessage id="game.code" />: <strong>{config.gameCode}</strong></p>
      <p className="setup-instructions"><FormattedMessage id="game.shareCode" /></p>
      
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="gameMasterName"><FormattedMessage id="game.masterName" /></label>
          <input
            type="text"
            id="gameMasterName"
            name="gameMasterName"
            value={config.gameMasterName}
            onChange={(e) => setConfig(prev => ({ ...prev, gameMasterName: e.target.value }))}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="totalPlayers"><FormattedMessage id="game.totalPlayers" /></label>
          <input
            type="number"
            id="totalPlayers"
            name="totalPlayers"
            min="6"
            max="16"
            value={config.totalPlayers}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="werewolves"><FormattedMessage id="game.werewolves" /></label>
          <input
            type="number"
            id="werewolves"
            name="werewolves"
            min="1"
            max="4"
            value={config.werewolves}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="seers"><FormattedMessage id="game.seers" /></label>
          <input
            type="number"
            id="seers"
            name="seers"
            min="0"
            max="1"
            value={config.seers}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="bodyguards"><FormattedMessage id="game.bodyguards" /></label>
          <input
            type="number"
            id="bodyguards"
            name="bodyguards"
            min="0"
            max="1"
            value={config.bodyguards}
            onChange={handleChange}
            required
          />
        </div>

        <div className="role-descriptions">
          <h3><FormattedMessage id="game.roleDescriptions" /></h3>
          <ul>
            <li><strong><FormattedMessage id="game.role.werewolves" /></strong></li>
            <li><strong><FormattedMessage id="game.role.seers" /></strong></li>
            <li><strong><FormattedMessage id="game.role.bodyguards" /></strong></li>
            <li><strong><FormattedMessage id="game.role.villagers" /></strong></li>
          </ul>
        </div>

        <button type="submit" className="start-game-btn"><FormattedMessage id="game.start" /></button>
      </form>
    </div>
  );
}; 