import React from 'react';
import { ROLES } from '../utils/constants';
import { FormattedMessage } from 'react-intl';

export function NightAction({ playerRole }) {
  if (!playerRole) return null;
  let promptId = '';
  if (playerRole === ROLES.WEREWOLF) promptId = 'game.nightAction.werewolf';
  if (playerRole === ROLES.SEER) promptId = 'game.nightAction.seer';
  if (playerRole === ROLES.BODYGUARD) promptId = 'game.nightAction.bodyguard';
  if (!promptId) return null;
  return (
    <div className="night-action">
      <h3><FormattedMessage id={promptId} /></h3>
      {/* Add player selection UI here */}
    </div>
  );
} 