import { ref, set, get, onValue, off } from 'firebase/database';
import { database } from './firebase';

class GameStateManager {
  constructor() {
    this.gameState = null;
    this.listeners = new Set();
    this.firebaseListener = null;
    this.currentGameRef = null;
    console.log('GameStateManager initialized');
  }

  async checkGameExists(gameCode) {
    console.log('Checking game existence for code:', gameCode);
    try {
      const gameRef = ref(database, `games/${gameCode}`);
      console.log('Firebase reference created:', gameRef);
      
      console.log('Attempting to get snapshot...');
      const snapshot = await get(gameRef);
      console.log('Snapshot received:', snapshot);
      console.log('Snapshot exists:', snapshot.exists());
      console.log('Snapshot value:', snapshot.val());
      
      return snapshot.exists();
    } catch (error) {
      console.error('Error checking game existence:', error);
      console.error('Error details:', {
        message: error.message,
        code: error.code,
        stack: error.stack
      });
      throw new Error('Failed to check if game exists: ' + error.message);
    }
  }

  async saveGameState(gameCode, state) {
    console.log('Saving game state:', { gameCode, state });
    try {
      if (!gameCode) {
        throw new Error('Game code is required');
      }

      // Create a clean state object without undefined values
      const cleanState = Object.entries(state).reduce((acc, [key, value]) => {
        if (value !== undefined) {
          acc[key] = value;
        }
        return acc;
      }, {});

      console.log('Clean state to save:', cleanState);
      
      // Save to sessionStorage instead of localStorage
      sessionStorage.setItem(`game_${gameCode}`, JSON.stringify(cleanState));
      console.log('State saved to sessionStorage');
      
      // Save to Firebase
      const gameRef = ref(database, `games/${gameCode}`);
      console.log('Firebase reference created:', gameRef);
      await set(gameRef, cleanState);
      console.log('Game state saved to Firebase successfully');
      
      // Update local state
      this.gameState = cleanState;
      console.log('Local game state updated:', this.gameState);
      
      // Notify listeners immediately
      this.notifyListeners();
      
      return cleanState;
    } catch (error) {
      console.error('Error saving game state:', error);
      throw new Error('Failed to save game state: ' + error.message);
    }
  }

  async loadGameState(gameCode) {
    console.log('Loading game state for code:', gameCode);
    try {
      // Try Firebase first
      const gameRef = ref(database, `games/${gameCode}`);
      console.log('Firebase reference created:', gameRef);
      const snapshot = await get(gameRef);
      
      if (snapshot.exists()) {
        const state = snapshot.val();
        console.log('Game state loaded from Firebase:', state);
        
        // Ensure all required fields are present
        const completeState = {
          ...state,
          phase: state.phase || 'setup',
          currentPhase: state.currentPhase || state.phase || 'setup',
          players: state.players || [],
          votes: state.votes || {},
          eliminatedPlayers: state.eliminatedPlayers || [],
          lastUpdate: state.lastUpdate || Date.now()
        };
        
        console.log('Complete game state after processing:', completeState);
        this.gameState = completeState;
        this.notifyListeners();
        return completeState;
      }

      // Fallback to sessionStorage instead of localStorage
      const localState = sessionStorage.getItem(`game_${gameCode}`);
      if (localState) {
        const state = JSON.parse(localState);
        console.log('Game state loaded from sessionStorage:', state);
        this.gameState = state;
        this.notifyListeners();
        return state;
      }

      console.log('No game state found');
      return null;
    } catch (error) {
      console.error('Error loading game state:', error);
      throw new Error('Failed to load game state: ' + error.message);
    }
  }

  async assignRoleToPlayer(gameCode, playerName, role) {
    console.log('Assigning role to player:', { gameCode, playerName, role });
    try {
      const state = await this.loadGameState(gameCode);
      if (!state) {
        throw new Error('Game not found');
      }

      const playerIndex = state.players.findIndex(p => p.name === playerName);
      if (playerIndex === -1) {
        throw new Error('Player not found');
      }

      state.players[playerIndex].role = role;
      await this.saveGameState(gameCode, state);
      return state;
    } catch (error) {
      console.error('Error assigning role:', error);
      throw error;
    }
  }

  async initializeGame(gameCode, players, gameMasterId, config) {
    console.log('Initializing game:', { gameCode, players, gameMasterId, config });
    try {
      if (!gameCode || !players || !gameMasterId) {
        throw new Error('Missing required parameters for game initialization');
      }

      const state = {
        gameCode,
        players: players.map(name => ({ name, role: null })),
        gameMasterId,
        phase: 'night',
        currentPhase: 'night',
        currentPlayer: null,
        votes: {},
        eliminatedPlayers: [],
        lastUpdate: Date.now(),
        config: {
          totalPlayers: config.totalPlayers,
          werewolves: config.werewolves,
          seers: config.seers,
          bodyguards: config.bodyguards
        }
      };

      console.log('Initial game state:', state);
      const savedState = await this.saveGameState(gameCode, state);
      console.log('Game state saved:', savedState);
      
      this.gameState = savedState;
      this.notifyListeners();
      
      return savedState;
    } catch (error) {
      console.error('Error initializing game:', error);
      throw error;
    }
  }

  subscribeToGameState(gameCode, callback) {
    console.log('Subscribing to game state for code:', gameCode);
    try {
      // Clean up existing subscription if any
      this.cleanupSubscription();

      // Create new reference
      this.currentGameRef = ref(database, `games/${gameCode}`);
      console.log('Firebase reference created for subscription:', this.currentGameRef);
      
      // Set up new listener
      this.firebaseListener = onValue(this.currentGameRef, (snapshot) => {
        if (snapshot.exists()) {
          const state = snapshot.val();
          console.log('Received game state update:', state);
          this.gameState = state;
          this.notifyListeners();
          if (callback) {
            callback(state);
          }
        } else {
          console.log('No game state found in subscription');
          if (callback) {
            callback(null);
          }
        }
      }, (error) => {
        console.error('Error in Firebase listener:', error);
        if (callback) {
          callback(null);
        }
      });

      this.listeners.add(callback);
      
      // Return cleanup function
      return () => {
        console.log('Cleaning up subscription for game:', gameCode);
        this.cleanupSubscription();
      };
    } catch (error) {
      console.error('Error subscribing to game state:', error);
      throw error;
    }
  }

  cleanupSubscription() {
    if (this.firebaseListener) {
      try {
        off(this.currentGameRef, 'value', this.firebaseListener);
      } catch (error) {
        console.error('Error cleaning up Firebase listener:', error);
      }
      this.firebaseListener = null;
    }
    this.currentGameRef = null;
  }

  notifyListeners() {
    console.log('Notifying listeners of state update:', this.gameState);
    if (!this.gameState) {
      console.log('No game state to notify listeners about');
      return;
    }
    
    this.listeners.forEach(callback => {
      try {
        console.log('Calling listener with state:', this.gameState);
        callback(this.gameState);
      } catch (error) {
        console.error('Error in listener callback:', error);
      }
    });
  }

  async quitGame(gameCode, playerName) {
    console.log('Quitting game:', { gameCode, playerName });
    try {
      const state = await this.loadGameState(gameCode);
      if (!state) {
        throw new Error('Game not found');
      }

      // Remove player from the game
      const updatedPlayers = state.players.filter(p => p.name !== playerName);
      
      // If no players left, delete the game
      if (updatedPlayers.length === 0) {
        console.log('No players left, deleting game');
        const gameRef = ref(database, `games/${gameCode}`);
        await set(gameRef, null);
        sessionStorage.removeItem(`game_${gameCode}`);
        sessionStorage.removeItem('currentGame');
        return null;
      }

      // Update game state
      const newState = {
        ...state,
        players: updatedPlayers
      };

      // Save updated state
      await this.saveGameState(gameCode, newState);
      
      // Clear session storage for this player
      sessionStorage.removeItem('currentGame');
      
      return newState;
    } catch (error) {
      console.error('Error quitting game:', error);
      throw new Error('Failed to quit game: ' + error.message);
    }
  }
}

export const gameStateManager = new GameStateManager(); 