import React from 'react';
import { FormattedMessage } from 'react-intl';

const LANGUAGE_OPTIONS = [
  { value: 'en', label: 'English' },
  { value: 'it', label: 'Italiano' }
];

export function LanguageSwitcher({ locale, setLocale }) {
  const handleLanguageChange = (e) => {
    const lang = e.target.value;
    setLocale(lang);
    localStorage.setItem('lupus_locale', lang);
  };

  return (
    <div className="language-switcher">
      <label htmlFor="lang-select">
        <FormattedMessage id="app.language" defaultMessage="Language:" />
      </label>
      <select id="lang-select" value={locale} onChange={handleLanguageChange}>
        {LANGUAGE_OPTIONS.map(opt => (
          <option key={opt.value} value={opt.value}>{opt.label}</option>
        ))}
      </select>
    </div>
  );
} 