import React from 'react';
import { QRCode } from './QRCode';
import { PHASES } from '../utils/constants';
import { FormattedMessage } from 'react-intl';

export function GameHeader({
  gameCode,
  phase,
  isGameMaster,
  onQuit
}) {
  return (
    <div className="game-header">
      <h1><FormattedMessage id="app.title" /></h1>
      <div className="game-info">
        <div className="game-code-display">
          <FormattedMessage id="game.code" />: <strong>{gameCode}</strong>
        </div>
        <div className="game-phase">
          <FormattedMessage id="game.currentPhase" />: <strong>{phase}</strong>
          {phase === PHASES.NIGHT ? '' : ''}
        </div>
      </div>
      {isGameMaster && gameCode && <QRCode gameCode={gameCode} />}
      <button 
        onClick={onQuit} 
        className="quit-game-btn"
        title={isGameMaster ? "Quit and end game for all players" : "Quit game"}
      >
        <FormattedMessage id="game.quit" />
      </button>
    </div>
  );
} 