import React, { useState, useEffect } from 'react';
import { Setup } from './Setup';
import { PlayerJoin } from './PlayerJoin';
import { FormattedMessage } from 'react-intl';

export const GameSetup = ({ onGameStart, onJoin }) => {
  const [view, setView] = useState('initial'); // 'initial', 'setup', 'join'

  useEffect(() => {
    // Check for game code in URL
    const urlParams = new URLSearchParams(window.location.search);
    const gameCode = urlParams.get('code');
    
    if (gameCode) {
      setView('join');
    }
  }, []);

  const handleGameStart = async (config) => {
    console.log('GameSetup: Starting game with config:', config);
    try {
      await onGameStart(config);
      // The game state will be updated by the parent component
    } catch (error) {
      console.error('GameSetup: Error starting game:', error);
      alert('Failed to start game: ' + error.message);
    }
  };

  const handleBack = () => setView('initial');

  if (view === 'setup') {
    return (
      <div className="game-setup">
        <Setup onGameStart={handleGameStart} />
        <button className="back-btn" onClick={handleBack}><FormattedMessage id="game.back" /></button>
      </div>
    );
  }

  if (view === 'join') {
    return (
      <div className="game-setup">
        <PlayerJoin onJoin={onJoin} />
        <button className="back-btn" onClick={handleBack}><FormattedMessage id="game.back" /></button>
      </div>
    );
  }

  // Initial screen
  return (
    <div className="game-setup">
      <img src="logo.png" alt="Lupus in Tabula" className="logo" width="280" />
      <h1><FormattedMessage id="game.welcome" /></h1>
      <p className="setup-instructions">
        <FormattedMessage id="game.choose" />
      </p>
      <div className="setup-options">
        <button
          className="setup-option-btn create-btn"
          onClick={() => setView('setup')}
        >
          <FormattedMessage id="game.create" />
        </button>
        <button
          className="setup-option-btn join-btn"
          onClick={() => setView('join')}
        >
          <FormattedMessage id="game.join" />
        </button>
      </div>
    </div>
  );
}; 