import React from 'react';
import { ROLE_DESCRIPTIONS } from '../utils/constants';
import { FormattedMessage } from 'react-intl';

export function RoleInfo({ playerRole }) {
  if (!playerRole) {
    return (
      <div className="role-info">
        <p className="role-title"><FormattedMessage id="game.waitingForPlayers" /></p>
        <p className="role-description"><FormattedMessage id="game.roleAssignedSoon" /></p>
      </div>
    );
  }
  return (
    <div className="role-info">
      <p className="role-title">
        <FormattedMessage id="game.role" defaultMessage="Your role:" />{' '}
        <span className={`role-badge role-${playerRole}`}>
          <FormattedMessage id={`game.role.${playerRole}`} defaultMessage={playerRole.charAt(0).toUpperCase() + playerRole.slice(1)} />
        </span>
      </p>
      <p className="role-description">{ROLE_DESCRIPTIONS[playerRole]}</p>
    </div>
  );
} 