import React from 'react';
import { ROLES } from '../utils/constants';
import { FormattedMessage } from 'react-intl';

export function PlayerList({ players, gameMasterName, isGameMaster, currentPlayerName, togglePlayerStatus }) {
  return (
    <div className="player-list">
      <h3><FormattedMessage id="game.playersAndRoles" /></h3>
      {players.map(player => {
        const isMaster = player.role === ROLES.MASTER || (player.name === gameMasterName && isGameMaster);
        return (
          <div key={player.name} className={`player-item ${!player.isAlive && !isMaster ? 'player-dead' : ''}`}>
            <span>{player.name}</span>
            <div className="player-controls">
              <span className="player-role">
                {isMaster ? (
                  <span className="role-badge role-master"><FormattedMessage id="game.role.master" /></span>
                ) : player.role ? (
                  <span className={`role-badge role-${player.role}`}>
                    <FormattedMessage id={`game.role.${player.role}`} defaultMessage={player.role.charAt(0).toUpperCase() + player.role.slice(1)} />
                  </span>
                ) : (
                  <span className="role-badge role-unassigned"><FormattedMessage id="game.role.unassigned" /></span>
                )}
              </span>
              {!isMaster && (
                <button
                  onClick={() => togglePlayerStatus(player.name)}
                  className={`status-btn ${player.isAlive ? 'alive' : 'dead'}`}
                  title={player.isAlive ? 'Mark as dead' : 'Mark as alive'}
                >
                  {player.isAlive ? <FormattedMessage id="game.alive" /> : <FormattedMessage id="game.dead" />}
                </button>
              )}
            </div>
          </div>
        );
      })}
    </div>
  );
} 