import React, { useState, useEffect } from 'react';
import { GameSetup } from './GameSetup';
import { QRCode } from './QRCode';
import { gameStateManager } from '../utils/gameState';
import '../styles/themes.css';
import '../styles/Game.css';
import { ROLES, ROLE_DESCRIPTIONS, PHASES } from '../utils/constants';
import { GameHeader } from './GameHeader';
import { GameStats } from './GameStats';
import { PlayerList } from './PlayerList';
import { GameControls } from './GameControls';
import { RoleInfo } from './RoleInfo';
import { NightAction } from './NightAction';
import { VotingAction } from './VotingAction';
import { FormattedMessage } from 'react-intl';

// Helper function to assign roles
const assignRoles = (players, config) => {
  console.log('Assigning roles with config:', config);
  
  // Create array of available roles based on config
  const availableRoles = [
    ...Array(config.werewolves).fill(ROLES.WEREWOLF),
    ...Array(config.seers).fill(ROLES.SEER),
    ...Array(config.bodyguards).fill(ROLES.BODYGUARD),
    ...Array(config.totalPlayers - config.werewolves - config.seers - config.bodyguards).fill(ROLES.VILLAGER)
  ];
  
  // Shuffle the roles array
  for (let i = availableRoles.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [availableRoles[i], availableRoles[j]] = [availableRoles[j], availableRoles[i]];
  }
  
  // Assign roles to players
  return players.map((player, index) => ({
    ...player,
    role: availableRoles[index]
  }));
};

export const Game = () => {
  const [gameState, setGameState] = useState(() => {
    // Try to restore game state from sessionStorage on initial load
    const savedState = sessionStorage.getItem('currentGame');
    if (savedState) {
      try {
        const parsedState = JSON.parse(savedState);
        console.log('Restored game state from sessionStorage:', parsedState);
        return parsedState;
      } catch (error) {
        console.error('Error parsing saved game state:', error);
      }
    }
    return {
      phase: PHASES.SETUP,
      players: [],
      currentPlayer: null,
      votes: {},
      gameOver: false,
      winner: null,
      isGameMaster: false,
      gameCode: null,
      playerRole: null,
      playerName: null,
      lastProtectedPlayer: null
    };
  });

  // Save game state to sessionStorage whenever it changes
  useEffect(() => {
    if (gameState.gameCode) {
      console.log('Saving game state to sessionStorage:', gameState);
      sessionStorage.setItem('currentGame', JSON.stringify(gameState));
    }
  }, [gameState]);

  // Set up Firebase listener when game code is available
  useEffect(() => {
    let cleanup = null;
    
    if (gameState.gameCode) {
      console.log('Setting up Firebase listener for game:', gameState.gameCode);
      try {
        cleanup = gameStateManager.subscribeToGameState(gameState.gameCode, (newState) => {
          if (newState === null) {
            // Game was deleted (master quit)
            if (!gameState.isGameMaster) {
              console.log('Game deleted from Firebase. Redirecting to home screen for non-master.');
              setGameState({
                phase: PHASES.SETUP,
                players: [],
                currentPlayer: null,
                votes: {},
                gameOver: false,
                winner: null,
                isGameMaster: false,
                gameCode: null,
                playerRole: null,
                playerName: null,
                lastProtectedPlayer: null
              });
              sessionStorage.removeItem('currentGame');
              // Optionally, redirect to home (if you have routing)
              // window.location.href = '/';
            } else {
              console.log('Master deleted the game, not redirecting self.');
            }
            return;
          }
          if (newState) {
            console.log('Received real-time update:', newState);
            // Check if master is present in the players list
            const masterPresent = newState.players && newState.players.some(p => p.role === ROLES.MASTER);
            if (!masterPresent && !gameState.isGameMaster) {
              console.log('Master is not present in the session. Quitting for non-master player.');
              setGameState({
                phase: PHASES.SETUP,
                players: [],
                currentPlayer: null,
                votes: {},
                gameOver: false,
                winner: null,
                isGameMaster: false,
                gameCode: null,
                playerRole: null,
                playerName: null,
                lastProtectedPlayer: null
              });
              sessionStorage.removeItem('currentGame');
              // Optionally, redirect to home (if you have routing)
              // window.location.href = '/';
              return;
            }
            setGameState(prev => ({
              ...prev,
              phase: newState.phase,
              currentPhase: newState.currentPhase,
              players: newState.players,
              votes: newState.votes,
              eliminatedPlayers: newState.eliminatedPlayers,
              gameOver: newState.gameOver,
              winner: newState.winner,
              config: newState.config,
              lastUpdate: newState.lastUpdate
            }));
          }
        });
      } catch (error) {
        console.error('Error setting up Firebase listener:', error);
      }
    }

    // Cleanup listener on unmount or when game code changes
    return () => {
      if (cleanup) {
        console.log('Cleaning up Firebase listener');
        cleanup();
      }
    };
  }, [gameState.gameCode]);

  // Update theme based on phase
  useEffect(() => {
    const isNightPhase = gameState.phase === PHASES.NIGHT;
    document.documentElement.setAttribute('data-theme', isNightPhase ? 'dark' : 'light');
  }, [gameState.phase]);

  const startGame = async (config) => {
    try {
      console.log('Starting game with config:', config);
      const gameMasterId = 'game-master-' + Date.now();
      
      // Create initial state
      const newState = {
        gameCode: config.gameCode,
        players: config.players.map(name => ({ 
          name, 
          role: ROLES.MASTER,
          isAlive: true 
        })),
        gameMasterId,
        isGameMaster: true,
        playerName: config.gameMasterName,
        phase: PHASES.NIGHT,
        currentPhase: PHASES.NIGHT,
        currentPlayer: null,
        votes: {},
        eliminatedPlayers: [],
        lastUpdate: Date.now(),
        config: {
          totalPlayers: config.totalPlayers,
          werewolves: config.werewolves,
          seers: config.seers,
          bodyguards: config.bodyguards
        }
      };

      console.log('Setting new game state:', newState);
      
      // Update local state first
      setGameState(newState);
      
      // Then save to Firebase
      await gameStateManager.saveGameState(config.gameCode, newState);
    } catch (error) {
      console.error('Error starting game:', error);
      alert('Failed to start game: ' + error.message);
    }
  };

  const joinGame = async ({ playerName, gameCode }) => {
    try {
      console.log('Joining game:', { playerName, gameCode });
      
      // Check if game exists
      const gameExists = await gameStateManager.checkGameExists(gameCode);
      if (!gameExists) {
        throw new Error('Game not found!');
      }

      // Get current game state
      const currentState = await gameStateManager.loadGameState(gameCode);
      if (!currentState) {
        throw new Error('Could not load game state');
      }

      // Check if player already exists
      const playerExists = currentState.players.some(p => p.name === playerName);
      if (playerExists) {
        throw new Error('A player with this name already exists in the game');
      }

      // Check if game is full
      if (currentState.players.length >= currentState.config.totalPlayers) {
        throw new Error('Game is full');
      }

      // Check if this is the game master trying to join
      const isGameMaster = currentState.gameMasterId && 
                          currentState.playerName === playerName;
      
      if (isGameMaster) {
        throw new Error('You cannot join as the game master. Please use a different name.');
      }

      // Add new player to the list
      const updatedPlayers = [...currentState.players, { 
        name: playerName, 
        role: null,
        isAlive: true 
      }];

      // If this is the last player, assign all roles
      let finalPlayers = updatedPlayers;
      if (updatedPlayers.length === currentState.config.totalPlayers) {
        console.log('All players joined, assigning roles');
        finalPlayers = assignRoles(updatedPlayers, currentState.config);
      }

      // Update shared game state
      const newSharedState = {
        ...currentState,
        players: finalPlayers,
        lastUpdate: Date.now()
      };

      // Find the role for the current player
      const currentPlayer = finalPlayers.find(p => p.name === playerName);
      const playerRole = currentPlayer ? currentPlayer.role : null;

      // Update local state with player-specific info
      const newLocalState = {
        ...newSharedState,
        playerName,
        gameCode,
        isGameMaster: false,
        playerRole
      };

      console.log('Updating game state for join:', newSharedState);

      // Update local state
      setGameState(newLocalState);

      // Save to Firebase (only the shared state)
      await gameStateManager.saveGameState(gameCode, newSharedState);
    } catch (error) {
      console.error('Error joining game:', error);
      throw error;
    }
  };

  const togglePlayerStatus = async (playerName) => {
    if (!gameState.isGameMaster) return;

    const updatedPlayers = gameState.players.map(player => {
      if (player.name === playerName) {
        return {
          ...player,
          isAlive: !player.isAlive
        };
      }
      return player;
    });

    const newState = {
      ...gameState,
      players: updatedPlayers
    };

    setGameState(newState);
    await gameStateManager.saveGameState(gameState.gameCode, newState);
  };

  const nextPhase = () => {
    if (!gameState.isGameMaster) return;

    const phaseOrder = [PHASES.NIGHT, PHASES.DAY, PHASES.VOTING];
    const currentIndex = phaseOrder.indexOf(gameState.phase);
    const nextPhase = phaseOrder[(currentIndex + 1) % phaseOrder.length];

    const newState = {
      ...gameState,
      phase: nextPhase,
      currentPhase: nextPhase
    };

    setGameState(newState);
    gameStateManager.saveGameState(gameState.gameCode, newState);
  };

  const handleQuit = async () => {
    try {
      if (!gameState.gameCode || !gameState.playerName) {
        throw new Error('No active game to quit');
      }

      console.log('Quitting game:', { gameCode: gameState.gameCode, playerName: gameState.playerName });
      
      // If game master is quitting, confirm first
      if (gameState.isGameMaster) {
        const confirmQuit = window.confirm(
          'Are you sure you want to quit? This will end the game for all players.'
        );
        if (!confirmQuit) return;
      }

      await gameStateManager.quitGame(gameState.gameCode, gameState.playerName);
      
      // Reset game state
      setGameState({
        phase: PHASES.SETUP,
        players: [],
        currentPlayer: null,
        votes: {},
        gameOver: false,
        winner: null,
        isGameMaster: false,
        gameCode: null,
        playerRole: null,
        playerName: null,
        lastProtectedPlayer: null
      });
    } catch (error) {
      console.error('Error quitting game:', error);
      alert('Failed to quit game: ' + error.message);
    }
  };

  console.log('Current game state:', gameState);

  if (gameState.phase === PHASES.SETUP || !gameState.phase) {
    return (
      <GameSetup 
        onGameStart={startGame}
        onJoin={joinGame}
      />
    );
  }

  return (
    <div className="game-container">
      <GameHeader
        gameCode={gameState.gameCode}
        phase={gameState.phase}
        isGameMaster={gameState.isGameMaster}
        onQuit={handleQuit}
      />

      {gameState.isGameMaster ? (
        <div className="game-master-view">
          <GameControls onNextPhase={nextPhase} />
          <GameStats players={gameState.players} config={gameState.config} />
          <PlayerList
            players={gameState.players}
            gameMasterName={gameState.playerName}
            isGameMaster={gameState.isGameMaster}
            currentPlayerName={gameState.playerName}
            togglePlayerStatus={togglePlayerStatus}
          />
        </div>
      ) : (
        <div className="player-view">
          <h2><FormattedMessage id="game.welcome" />{gameState.playerName ? `, ${gameState.playerName}!` : ''}</h2>
          <RoleInfo playerRole={gameState.playerRole} />
          <p><FormattedMessage id="game.currentPhase" />: {gameState.phase}</p>
          {gameState.phase === PHASES.NIGHT && gameState.playerRole && (
            <NightAction playerRole={gameState.playerRole} />
          )}
          {gameState.phase === PHASES.VOTING && (
            <VotingAction />
          )}
        </div>
      )}
    </div>
  );
}; 