import React, { useState, useEffect } from 'react';
import { FormattedMessage } from 'react-intl';

export const PlayerJoin = ({ onJoin }) => {
  const [playerName, setPlayerName] = useState('');
  const [gameCode, setGameCode] = useState('');
  const [error, setError] = useState('');
  const [isJoining, setIsJoining] = useState(false);

  useEffect(() => {
    // Get game code from URL query parameters
    const params = new URLSearchParams(window.location.search);
    const codeFromUrl = params.get('code');
    if (codeFromUrl) {
      setGameCode(codeFromUrl.toUpperCase());
    }
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!playerName.trim()) {
      setError('Please enter your name');
      return;
    }
    if (!gameCode.trim()) {
      setError('Please enter the game code');
      return;
    }

    try {
      setIsJoining(true);
      setError('');
      console.log('Attempting to join game:', { playerName, gameCode });
      await onJoin({ playerName, gameCode });
    } catch (error) {
      console.error('Error joining game:', error);
      setError(error.message || 'Failed to join game');
    } finally {
      setIsJoining(false);
    }
  };

  return (
    <div className="player-join">
      <h2><FormattedMessage id="game.joinGame" /></h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="playerName"><FormattedMessage id="game.yourName" /></label>
          <input
            type="text"
            id="playerName"
            value={playerName}
            onChange={(e) => setPlayerName(e.target.value)}
            placeholder={/* intl string */ undefined}
            disabled={isJoining}
            autoFocus
          />
        </div>
        <div className="form-group">
          <label htmlFor="gameCode"><FormattedMessage id="game.code" /></label>
          <input
            type="text"
            id="gameCode"
            value={gameCode}
            onChange={(e) => setGameCode(e.target.value.toUpperCase())}
            placeholder={/* intl string */ undefined}
            maxLength={6}
            disabled={isJoining}
          />
        </div>
        {error && <p className="error-message">{error}</p>}
        <button 
          type="submit" 
          className="join-game-btn"
          disabled={isJoining}
        >
          {isJoining ? <FormattedMessage id="game.joining" /> : <FormattedMessage id="game.joinGame" />}
        </button>
      </form>
    </div>
  );
}; 