import React from 'react';
import { FormattedMessage } from 'react-intl';

export function GameControls({ onNextPhase }) {
  return (
    <div className="game-controls">
      <h2><FormattedMessage id="game.masterControls" /></h2>
      <button onClick={onNextPhase} className="phase-btn">
        <FormattedMessage id="game.nextPhase" />
      </button>
    </div>
  );
} 