import React from 'react';
import { QRCodeSVG } from 'qrcode.react';
import { FormattedMessage } from 'react-intl';

export const QRCode = ({ gameCode }) => {
  const gameUrl = `https://rmarchet.github.io/lupus/?code=${gameCode}`;

  return (
    <div className="qr-code-container">
      <QRCodeSVG
        value={gameUrl}
        size={200}
        level="H"
        includeMargin={true}
        className="qr-code"
      />
      <p className="qr-code-text"><FormattedMessage id="game.scanToJoin" /></p>
      <p className="qr-code-url">
        <a href={gameUrl} target="_blank" rel="noopener noreferrer">{gameUrl}</a>
      </p>
    </div>
  );
}; 